import {Component, EventEmitter, HostListener, Input, OnInit, Output} from '@angular/core';
import {Item} from '../../types';
import {GearPopupService} from './gear-popup.service';

@Component({
  selector: 'app-gear-item, [app-gear-item]',
  template: '<span [style.backgroundImage]="bg">{{ item?.name }} ({{ item?.ilvl }})</span>',
  styles: ['span{background-repeat: no-repeat; background-size: contain; padding-left: 20px;}']
})
export class GearItemComponent implements OnInit {
  @Input('app-gear-item') item?: Item;
  @Output() clicked = new EventEmitter<MouseEvent>();
  bg?: string;

  constructor(private popup: GearPopupService) {
  }

  ngOnInit(): void {
    this.bg = `url('https://wow.zamimg.com/images/wow/icons/medium/${this.item?.icon}.jpg')`;
  }

  @HostListener('mouseenter', ['$event'])
  mouseenter(event: MouseEvent): void {
    if (this.item) {
      this.popup.open(event, this.item.tooltip);
    }
  }

  @HostListener('mousemove', ['$event'])
  mousemove(event: MouseEvent): void {
    this.popup.move(event);
  }

  @HostListener('mouseleave')
  mouseleave(): void {
    this.popup.close();
  }

  @HostListener('click', ['$event'])
  click(event: MouseEvent): void {
    if (event.altKey) {
      window.open(`https://wowhead.com/item=${this.item?.id}&bonus=${this.item?.bonus.join(':')}`, '_blank');
    } else {
      this.clicked.emit(event);
    }
  }
}
