import {Injectable, SecurityContext} from '@angular/core';
import {Item, Slot} from '../types';
import {HttpClient} from '@angular/common/http';
import {DomSanitizer} from '@angular/platform-browser';

@Injectable({
  providedIn: 'root'
})
export class GearItemService {
  private cache: Map<string, Promise<Result>> = new Map();

  constructor(private http: HttpClient, private sanitizer: DomSanitizer) {
  }

  async getResultForItem(item: { id: number, bonus: number[], slot: Slot, enchant?: number }): Promise<Item> {
    const url = this.createUrl(item);
    if (!this.cache.get(url)) {
      this.cache.set(url, this.fetch(url));
    }
    const result = await this.cache.get(url);
    if (!result) {
      throw new Error('No result found');
    }
    return {
      id: item.id,
      bonus: item.bonus,
      slot: item.slot,
      enchant: item.enchant,
      name: result.name,
      icon: result.icon,
      tooltip: this.sanitizer.sanitize(SecurityContext.HTML, result.tooltip) ?? '',
      ilvl: result.ilvl,
      class: result.tooltip.match(/q(\d+)/)?.[0] ?? '',
    };
  }

  private async fetch(url: string): Promise<Result> {
    const result = await this.http.get(url).toPromise() as any;
    return {
      tooltip: result.tooltip,
      name: result.name,
      ilvl: parseInt(result.tooltip.match(/<!--ilvl-->(\d+)/)?.[1] ?? '', 10),
      icon: result.icon,
    };
  }


  private createUrl(item: { id: number, bonus: number[], slot: Slot, enchant?: number }): string {
    return `https://wwww.wowhead.com/tooltip/item/${item.id}?` + Object.entries({
      bonus: item.bonus.join(':'),
      ench: item.enchant,
    }).filter(a => a[1]).map(a => a.join('=')).join('&');
  }
}

interface Result {
  tooltip: string;
  name: string;
  ilvl: number;
  icon: string;
}
