import {NgModule} from '@angular/core';
import {BrowserModule} from '@angular/platform-browser';

import {AppComponent} from './app.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {GearItemComponent} from './gear-item/gear-item.component';
import {HttpClientModule} from '@angular/common/http';
import {GearPopupComponent} from './gear-item/gear-popup/gear-popup.component';
import {OverlayModule} from '@angular/cdk/overlay';
import {MatButtonModule} from '@angular/material/button';
import {MatDialogModule} from '@angular/material/dialog';
import {FormsModule} from '@angular/forms';
import {MatInputModule} from '@angular/material/input';
import {MatFormFieldModule} from '@angular/material/form-field';
import {ClipboardModule} from '@angular/cdk/clipboard';
import {MatTooltipModule} from '@angular/material/tooltip';
import {ClickTooltipComponent, ClickTooltipDirective} from './click-tooltip.directive';

declare global {
  interface String {
    matchAll: (re: RegExp) => RegExpMatchArray[];
  }
}

String.prototype.matchAll = function(re: RegExp): RegExpMatchArray[] {
  const value = this as string;
  const matches: RegExpMatchArray[] = [];
  while (true) {
    const match = re.exec(value);
    if (!match) {
      break;
    }
    matches.push(match);
  }
  return matches;
};

@NgModule({
  declarations: [
    AppComponent,
    GearItemComponent,
    GearPopupComponent,
    ClickTooltipDirective,
    ClickTooltipComponent,
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    HttpClientModule,
    OverlayModule,
    MatButtonModule,
    MatDialogModule,
    FormsModule,
    MatInputModule,
    MatFormFieldModule,
    ClipboardModule,
    MatTooltipModule,
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule {
}
