import {Component, Directive, HostListener, Input, OnDestroy, ViewContainerRef} from '@angular/core';
import {Overlay, OverlayRef} from '@angular/cdk/overlay';
import {ComponentPortal} from '@angular/cdk/portal';

@Component({
  selector: 'app-click-tooltip',
  template: '<div class="mat-tooltip">{{ message }}</div>',
  styles: [`
    .mat-tooltip {
      color: #fff;
      border-radius: 4px;
      margin: 5px;
      max-width: 250px;
      padding-left: 8px;
      padding-right: 8px;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  `]
})
export class ClickTooltipComponent {
  message?: string;
}

@Directive({
  selector: '[appClickTooltip]'
})
export class ClickTooltipDirective implements OnDestroy {
  @Input('appClickTooltip') message?: string;
  ref?: OverlayRef;
  private timer: any;

  constructor(private overlay: Overlay, private elementRef: ViewContainerRef) {
  }

  @HostListener('click')
  click(): void {
    if (!this.message) {
      return;
    }
    this.close();
    this.ref = this.overlay.create({
      positionStrategy: this.overlay.position().flexibleConnectedTo(this.elementRef.element)
        .withPositions([{
          originX: 'center',
          originY: 'top',
          overlayY: 'bottom',
          overlayX: 'center',
        }]),
    });
    const ref = this.ref.attach(new ComponentPortal(ClickTooltipComponent));
    ref.instance.message = this.message;
    this.timer = setTimeout(() => this.close(), 2000);
  }

  ngOnDestroy(): void {
    this.close();
  }

  private close(): void {
    if (this.timer) {
      clearTimeout(this.timer);
      this.timer = undefined;
    }
    if (this.ref?.hasAttached()) {
      this.ref?.detach();
    }
  }
}
