import {Component, ElementRef, TemplateRef, ViewChild} from '@angular/core';
import {classes, getSlotKey, Item, Slot, slotKeys, slots} from '../types';
import {GearItemService} from './gear-item.service';
import {MatDialog} from '@angular/material/dialog';
import {first} from 'rxjs/operators';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'test';
  gear: Map<Slot, Item[]> = new Map();
  categories?: Map<string, Map<Slot, Item[]>>;
  slots = slots;
  selected: Item[] = [];
  output = '';
  name = '';
  input = '';
  dialogInput = '';
  mainEnchants: Map<Slot, number | undefined> = new Map();

  constructor(private service: GearItemService, private dialog: MatDialog) {
  }

  @ViewChild('importButton', {read: ElementRef})
  set setImportButton(element: ElementRef<HTMLElement>) {
    if (this.input === '') {
      element.nativeElement.click();
    }
  }

  updateOutput(): void {
    this.output = this.input.replace(/\n+$/, '')
        .replace(/^level=.*$/m, m => `${m}\ncalculate_scale_factors=0`)
      + '\n\n';
    this.output += this.selected.map(item => {
      return [
        `copy="${item.name} (${item.ilvl}) - ${item.slot},${this.name}"`,
        `${getSlotKey(item.slot)}=,${Object.entries({
          id: item.id,
          bonus_id: item.bonus.join('/'),
          enchant_id: item.enchant,
        }).filter(a => a[1]).map(s => s.join('=')).join(',')}`,
      ].join('\n');
    }).join('\n\n');
  }

  private parseData(input: string): any {
    this.input = input;
    Promise.all(input.matchAll(new RegExp(`^(?<slot>${slotKeys.join('|')})=("?)(.*)\\2$`, 'gm'))
      .map(s => this.mapToItem(s[0])))
      .then(r => this.mapPerSlot(r))
      .then(r => {
        this.gear = r;
        this.mainEnchants.clear();
        for (const item of this.gear.values()) {
          this.mainEnchants.set(item[0].slot, item[0].enchant);
        }
        this.getPerCategory(input).then(r2 => {
          this.categories = r2;
          this.updateOutput();
        });
      });
    this.name = input.match(/^name=("?)(.*)\1/m)?.[2] ?? input.match(new RegExp(`^(${classes.join('|')})=("?)(.*)\\2$`, 'm'))?.[3] ?? 'Onbekend';
    this.selected = [];
  }

  private async mapToItem(line: string): Promise<Item> {
    let enchant: any = line.match(/enchant_id=(\d+)/)?.[1];
    const slot = Slot[line.split('=')[0] as keyof typeof Slot];
    if (enchant) {
      enchant = parseInt(enchant, 10);
    } else {
      enchant = this.mainEnchants.get(slot);
    }
    return this.service.getResultForItem({
      id: parseInt(line.match(/,id=(\d+)/)?.[1] ?? '', 10),
      bonus: line.match(/bonus_id=(((\d+)\/?)+)/)?.[1].split('/').map(s => parseInt(s, 10)) ?? [],
      slot,
      enchant,
    });
  }

  private mapPerSlot(items: Item[]): Map<Slot, Item[]> {
    const ret: Map<Slot, Item[]> = new Map();
    for (const item of items) {
      if (!ret.has(item.slot)) {
        ret.set(item.slot, []);
      }
      ret.get(item.slot)?.push(item);
    }

    return ret;
  }

  private async getPerCategory(input: string): Promise<Map<string, Map<Slot, Item[]>>> {
    const re = new RegExp(`^# (${slotKeys.join('|')})=(.*)$`);
    const lines = input.split('\n');
    const categories: Map<string, Map<Slot, Item[]>> = new Map();
    let current: Map<Slot, Item[]> = new Map();
    const promises: Promise<void>[] = [];

    const addItem = async (cur: Map<Slot, Item[]>, promise: Promise<Item>): Promise<void> => {
      const item = await promise;
      if (!cur.get(item.slot)) {
        cur.set(item.slot, []);
      }
      cur.get(item.slot)?.push(item);
      if (item.slot === Slot.finger1 || item.slot === Slot.trinket1) {
        const duplicate = JSON.parse(JSON.stringify(item));
        duplicate.slot = Slot.finger1 ? Slot.finger2 : Slot.trinket2;
        await addItem(cur, new Promise<Item>(resolve => resolve(duplicate)));
      }
    };

    for (const line of lines) {
      if (line.startsWith('### ')) {
        current = new Map();
        categories.set(line.substring(4), current);
      } else if (line.match(re)) {
        promises.push(addItem(current, this.mapToItem(line.substring(2))));
      }
    }
    await Promise.all(promises);
    return categories;
  }

  itemSelected(item: Item): boolean {
    return this.selectedItemIndex(item) !== -1;
  }

  private selectedItemIndex(item: Item): number {
    return this.selected.findIndex(s => s.id === item.id && s.bonus.sort().join('') === item.bonus.sort().join('') && s.slot === item.slot);
  }

  toggleSelect(item: Item): void {
    const index = this.selectedItemIndex(item);
    if (index === -1) {
      this.selected.push(item);
    } else {
      this.selected.splice(index, 1);
    }
    this.updateOutput();
  }

  openDialog(element: TemplateRef<HTMLElement>): void {
    this.dialog.open(element, {
      width: '50vw',
    }).afterClosed().pipe(first()).subscribe(s => {
      if (!!s) {
        this.parseData(s);
      }
    });
  }
}
