export enum Slot {
  head = 'Head',
  neck = 'Neck',
  shoulder = 'Shoulder',
  back = 'Back (cloak)',
  chest = 'Chest',
  wrist = 'Wrist',
  hands = 'Hands (gloves)',
  waist = 'Waist',
  legs = 'Legs',
  feet = 'Feet',
  finger1 = 'Finger (ring) 1',
  finger2 = 'Finger (ring) 2',
  trinket1 = 'Trinket 1',
  trinket2 = 'Trinket 2',
  main_hand = 'Main hand',
  off_hand = 'Off hand',
}

export enum Class {
  death_knight = 'death_knight',
  deathknight = 'deathknight',
  demonhunter = 'demonhunter',
  demon_hunter = 'demon_hunter',
  druid = 'druid',
  hunter = 'hunter',
  mage = 'mage',
  monk = 'monk',
  paladin = 'paladin',
  priest = 'priest',
  shaman = 'shaman',
  rogue = 'rogue',
  warlock = 'warlock',
  warrior = 'warrior',
}

export interface Item {
  id: number;
  bonus: number[];
  slot: Slot;
  enchant?: number;
  name: string;
  icon: string;
  tooltip: string;
  ilvl: number;
  class: string;
}

export interface Category {
  name: string;
  items: Map<Slot, Item[]>;
}

export interface Gearset {
  name: string;
  items: Item[];
  expanded: boolean;
}

export const slots: Slot[] = Object.keys(Slot).map(s => Slot[s as keyof typeof Slot]);
export const classes: Class[] = Object.keys(Class).map(s => Class[s as keyof typeof Class]);
export const slotKeys: string[] = Object.keys(Slot);
export const getSlotKey = (slot: Slot): keyof Slot => {
  return Object.entries(Slot).find(s => s[1] === slot)?.[0] as keyof Slot;
};
