import {Injectable} from '@angular/core';
import {Overlay, OverlayRef, PositionStrategy} from '@angular/cdk/overlay';
import {ComponentPortal} from '@angular/cdk/portal';
import {GearPopupComponent} from './gear-popup/gear-popup.component';

@Injectable({
  providedIn: 'root'
})
export class GearPopupService {
  private ref?: OverlayRef;

  constructor(private overlay: Overlay) {
  }

  open({x, y}: MouseEvent, content: string): void {
    this.close();
    this.ref = this.overlay.create({
      positionStrategy: this.createPositionStrategy(x, y)
    });
    const componentRef = this.ref.attach(new ComponentPortal(GearPopupComponent));
    componentRef.instance.tooltip = content;
  }

  private createPositionStrategy(x: number, y: number): PositionStrategy {
    const offset = 15;
    return this.overlay.position()
      .flexibleConnectedTo({x, y})
      .withPositions([{
        originX: 'end',
        originY: 'bottom',
        overlayX: 'start',
        overlayY: 'top',
        offsetX: offset,
        offsetY: offset,
      }, {
        originX: 'end',
        originY: 'bottom',
        overlayX: 'start',
        overlayY: 'bottom',
        offsetX: offset,
        offsetY: -offset,
      }, {
        originX: 'end',
        originY: 'bottom',
        overlayX: 'start',
        overlayY: 'bottom',
        offsetX: offset,
        offsetY: 0,
      }, {
        originX: 'end',
        originY: 'bottom',
        overlayX: 'end',
        overlayY: 'top',
        offsetX: -offset,
        offsetY: offset,
      }, {
        originX: 'end',
        originY: 'bottom',
        overlayX: 'end',
        overlayY: 'bottom',
        offsetX: -offset,
        offsetY: -offset,
      }, {
        originX: 'end',
        originY: 'bottom',
        overlayX: 'end',
        overlayY: 'bottom',
        offsetX: -offset,
        offsetY: 0,
      }]);
  }

  close(): void {
    if (this.ref?.hasAttached()) {
      this.ref?.detach();
    }
  }

  move({x, y}: MouseEvent): void {
    this.ref?.updatePositionStrategy(this.createPositionStrategy(x, y));
  }
}
